import {
  jpeg_encode,
  quant_process,
  optipng_compress,
} from '@cmao/compress-img';

const ctx:Worker = self as any;
const sendMessage:any = self.postMessage;

ctx.addEventListener('message', async (message) => {
  const receive_data = message.data;
  const type = receive_data.type;
  const data = receive_data.data;
  const buffer = receive_data.buffer;
  const id = receive_data.id;
  let compressed_buffer;
  switch (type) {
    case 'mozjpeg':
      compressed_buffer = await jpeg_encode(data);
      sendMessage({
        type: 'compress_jpeg_done',
        data: compressed_buffer,
        id,
      });
      break;
    case 'optipng':
      compressed_buffer = await optipng_compress(buffer);
      sendMessage({
        type: 'compress_png_done',
        data: compressed_buffer,
        id,
      });
      break;
    case 'transparent_png':
      const compressed_data = await quant_process(data);
      sendMessage({
        type: 'compress_transparent_png_done',
        data: compressed_data,
        id,
      });
      break;
  }
});